.page-head-title {
  background: linear-gradient(
    180deg,
    #066877 20.63%,
    rgba(19, 181, 130, 0) 143.12%
  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;

}
.container {
    width: 100% !important;
  /* max-width: 1335px; */
  margin: 0 auto;
}
.grid-row {
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-start;
}
.grid-item {
  min-height: 80px;
  flex-basis: 25%;
  -ms-flex: auto;
  /* width: 259px; */
  position: relative;
  padding: 10px;
  box-sizing: border-box;
}
.grid-item-container {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;

}
.book-icon-div {
    flex-basis: 40%;
    height: 100%;
    position: relative;
    display: flex;
    justify-content: center;
    

}
.book-icon-div img {
    width: 50%;;
    flex-basis: 50%;
    align-self: center;
    justify-self: center;
    


}
.book-infos-div {
    flex-basis: 60%;
    height: 100%;
    position: relative;
    display: flex;
    flex-direction: column;
}
.book-infos-div div {
    width: 100%;
    padding: 2% 5%;

}
.book-infos-cours-title {
    flex-basis: 30%;
    min-height: 25px;
    
    font-weight: bold;
    font-size: 1.1rem;
  

}
.book-infos-pdf-title {
    flex-basis: 30%;
    min-height: 25px;
    
}
.book-infos-timestamp {
    font-size: 0.7rem;
}

@media (max-width: 1333px) {
  .grid-item {
    flex-basis: 20%;
  }
}
@media (max-width: 1073px) {
  .grid-item {
    flex-basis: 25%;
  }
}
@media (max-width: 815px) {
  .grid-item {
    flex-basis: 50%;
  }
}
@media (max-width: 555px) {
  .grid-item {
    flex-basis: 100%;
  }
}
