.classe-recapitulatif {
  list-style: none;
  padding: 0.3rem;
  width: 33%;
  margin: 0 5px;
  border: 1px solid #13b582;
}

.classe-recapitulatif li {
  border: none;
  text-align: center;
}

.classe-recapitulatif li.classe-id {
  text-decoration: underline;
  font-weight: bold;
  color: var(--dark-green);
}

.classe-recapitulatif.mono {
  border: none;
}
