.abonnement-cycle .header .eleves {
  display: flex;
  justify-content: center;
}

.abonnement-cycle .header.multiple {
  margin: 0;
}

.abonnement-cycle .content {
  justify-content: center;
}

.abonnement-step.abonnement-cycle .content .list-item {
  background: url("/assets/img/type_abonnement2.jpg") center center / cover;
  padding: 1rem;
  margin-bottom: 2rem;
  border: 1px solid #f0f0f0;
}

.abonnement-step.abonnement-cycle .content.multiple li {
  margin-bottom: 1rem;
}

.abonnement-cycle .content .list-item::before {
  border: 1px solid #f0f0f0;
}

.abonnement-cycle .content .list-item:nth-of-type(1) {
  background: url("/assets/img/type_abonnement1.jpg") center center / cover;
  justify-self: flex-start;
}
.abonnement-cycle .content .list-item:nth-of-type(1):before {
  background: rgba(255, 133, 21, 0.8);
  justify-self: flex-start;
}

.abonnement-cycle .content .list-item:nth-of-type(2):before {
  justify-self: center;
  background: rgba(80, 80, 80, 0.8);
}
.abonnement-cycle .content .list-item:nth-of-type(3)::before {
  justify-self: flex-end;
  background: rgba(5, 132, 250, 0.8);
}

@media (min-height: 680px) {
  .abonnement-step.abonnement-cycle .content.multiple li {
    margin-bottom: 2rem;
  }
}

@media (min-height: 790px) {
  .abonnement-step.abonnement-cycle .content .list-item,
  .abonnement-step.abonnement-cycle .content.multiple li {
    margin-bottom: 3rem;
  }
}
@media (min-height: 920px) {
  .abonnement-step.abonnement-cycle .content .list-item,
  .abonnement-step.abonnement-cycle .content .list-item {
    margin-bottom: 4rem;
  }
}

.modal-classe {
  text-align: center;
}

.modal-classe .header,
.modal-classe .content {
  z-index: 100;
}

.modal-classe .header hr {
  width: 80%;
  border: 1px solid #adadad;
  margin: 0 auto;
}

.modal-classe .header p {
  font-weight: 400;
  font-size: 1rem;
  line-height: 1.8rem;
  margin: 1rem 0 0.5rem 0;
  color: rgba(0, 0, 0, 0.7);
}

.modal-classe .content {
  margin-top: 2rem;
}

.modal-classe .actions {
  position: absolute;
  bottom: 1.8rem;
  left: 0;
  right: 0;
}

.modal-classe .actions button {
  background: var(--dark-green);
  font-weight: 400;
  font-size: 1rem;
  border-radius: 5px;
  line-height: 1.8rem;
  width: 70%;
  margin: 0 auto;
  padding: 0.5rem;
}
