.classe-item {
  display: flex;
  margin: 0 0 1.5rem 1.5rem;
  align-items: center;
  cursor: pointer;
}

.classe-item.active .radio {
  background: #13b582;
}

.classe-item .radio {
  margin-right: 1rem;
  background: #ececec;
  border: 1px solid #cdcdcd;
  box-sizing: border-box;
  width: 1.2rem;
  height: 1.2rem;
  border-radius: 50%;
}

.classe-item p {
  margin: 0;
  text-align: left;
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 1rem;
  line-height: 1.8rem;
  color: rgba(0, 0, 0, 0.9);
}
