.inscription {
  position: relative;
}
.inscription__content {
  margin-top: 60vw;
}
.inscription__title {
  font-size: 1.1rem;
  font-weight: 600;
  margin-bottom: 1rem;
  color: var(--dark-green);
  text-align: center;
}

.inscription__form {
  width: 80%;
  margin: 0 auto;
  padding-top: 0;
  min-height: 60vh;
  max-width: 40rem;
  background: white;
  position: relative;
  z-index: 1;
  background: transparent;
  display: flex;
  justify-content: space-evenly;
  flex-direction: column;
}

.inscription__form .inscription__form-sinscrire {
  background: var(--dark-green);
  font-weight: 700;
  font-size: 1rem;
  width: 100%;
  padding: 0.7rem;
}

.inscription footer {
  position: relative;
  height: 30vh;
  margin-top: -15vh;
  padding-top: 15vh;
}
.inscription footer::before {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(
      180deg,
      #ffffff 7.56%,
      rgba(255, 255, 255, 0) 160.65%
    ),
    url("/assets/img/footer_bg.jpeg") no-repeat center center;
  opacity: 0.35;
}
.inscription__footer-content {
  text-align: center;
  position: absolute;
  bottom: 0;
  top: 15vh;
  right: 0;
  left: 0;
}

.inscription__footer-content p {
  margin: 0.5rem;
  color: #505050;
}

.inscription__footer-actions {
  display: flex;
  justify-content: center;
  align-items: center;
}

hr {
  flex-grow: 1;
  border: 1px solid #13b582;
}

.inscription__login {
  margin: 0 1rem;
  padding: 0;
  font-size: 1rem;
  font-weight: 700;
  color: #13b582;
}

@media (min-height: 905px) {
  .inscription footer {
    height: 35vh;
  }
}

.login {
  height: 100vh;
  overflow: hidden;
}
.login form {
  min-height: unset;
  min-height: 30vh;
}
.login footer {
  height: 50vh;
}
@media (min-height: 658px) {
  .login footer {
    height: 60vh;
  }
}
@media (min-height: 750px) {
  .login footer {
    height: 70vh;
  }
}
@media (min-height: 815px) {
  .login footer {
    height: 80vh;
  }
}
@media (min-height: 880px) {
  .login footer {
    height: 90vh;
  }
}
