.avatar-classe {
  font-size: 0.8rem;
  display: flex;
  flex-direction: column;
  margin: 0 1rem;
  justify-content: center;
  align-items: center;
  color: rgba(77, 77, 77, 0.6);
}
.avatar-classe .avatar {
  border-radius: 50%;
  color: var(--dark-green);
  margin: 0;
  width: 30px;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 600;

  box-shadow: 2px 2px 10px rgba(77, 77, 77, 0.6);
  margin-bottom: 4px;
}

.avatar-classe.active .avatar {
  background: var(--dark-green);
  color: white;
}

.avatar-classe.cem-select .avatar {
  background: rgba(80, 80, 80, 0.8);
  color: white;
}
.avatar-classe.primaire-select .avatar {
  background: rgba(255, 133, 21, 0.8);
  color: white;
}
.avatar-classe.lycee-select .avatar {
  background: rgba(5, 132, 250, 0.8);
  color: white;
}
