.cards-quiz {
  width: 100%;
  height: 100%;
  background: white;
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  z-index: 50;
  display: flex;
  overflow: hidden;
  flex-direction: column;
  justify-content: space-between;
}

.cards-quiz__cards {
  width: 100%;
  flex-grow: 1;
  align-self: center;
  justify-self: center;
  border-radius: 6px;
  position: relative;
}
.cards-quiz__results {
  height: 6vh;
  z-index: 50;
  background: #0fd79c;
  width: 100%;
  display: flex;
  padding: 1.5rem 2rem 1rem 2rem;
  align-items: center;
  justify-content: space-between;
  position: relative;
}

.cards-quiz__results svg {
  position: relative;
}

.cards-quiz__results .swipe-score {
  position: relative;
}

.cards-quiz__results .cards-count {
  position: absolute;
  width: 15px;
  height: 15px;
  background: red;
  color: white;
  font-weight: 1000;
  font-size: 0.7rem;
  border-radius: 50%;
  top: -3px;
  right: 2px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.slide-quiz__title-bg {
  position: relative;
  z-index: 50;

  height: 7vh;
  display: flex;
  align-items: center;
  background: linear-gradient(
      270deg,
      rgba(196, 196, 196, 0) -5.33%,
      #ffffff 51.87%
    ),
    url(/assets/img/arched-header_bg.jpg) center center / cover;
}

.slide-quiz__title-bg .slide-prev {
  cursor: pointer;
}
.slide-quiz__title-bg .slide-prev * {
  stroke: var(--dark-green);
}

.slide-quiz__title-bg h2 {
  color: var(--dark-green);
  font-size: 1rem;
  line-height: 100%;
  margin: 0;
}
