.form-control {
  margin-bottom: 1rem;
}
.form-control input,
.form-control textarea,
.form-control select {
  width: 100%;
  padding: 0.75rem;
  background: #f0f0f0;
  border: 1px solid var(--dark-green);
  border-radius: 5px;
  outline: var(--dark-green);
  -moz-user-select: none;
  webkit-user-select: none;
  ms-user-select: none;
  user-select: none;
}

.form-control-invalid input,
.form-control-invalid textarea,
.form-control-invalid select {
  border: 1px solid red;
}

.form-control-invalid p {
  color: red;
  font-size: 0.8rem;
  margin: 0.2rem 0 0 0;
}

.form-control select {
  cursor: pointer;
}

.form-control input::placeholder,
.form-control textarea::placeholder,
.form-control select.placeholder {
  color: rgba(0, 0, 0, 0.29);
}

.input-icon {
  position: relative;
  display: flex;
  align-items: center;
}

.input-icon input {
  padding-right: 30px;
}

.icon {
  right: 5px;
  top: 50%;
  transform: translateY(-50%);
  position: absolute;
  cursor: pointer;
}

.icon.active path {
  stroke: var(--dark-green);
  stroke-width: 2;
}
