.links-drawer {
  list-style: none;
  padding: 0;
  margin: 0 0 2rem 0;
}
.links-drawer li {
  /* margin: 0.75rem 0; */
  padding: 0.5rem;
  min-height: 10px;
  transition: all 0.8s;
}
.links-drawer li:hover {
  background-color: rgba(240, 248, 255,0.4);
}
.links-drawer li.active {
  background-color: rgba(240, 248, 255,0.7);
}

.links-drawer a {
  text-decoration: none;
  color: white;
  font-size: 1.1rem;
}
