/* Cards List */
.cards-list {
  position: relative;
  height: 100%;
  width: 200%;
}

.cards-list.cards-end {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.cards-list.cards-end .card {
  left: 0;
  position: relative;
}

.card {
  position: absolute;
  background: white;
  width: 70vw;
  height: 90%;
  left: -50vw;
  border-radius: 6px;
  cursor: pointer;
  transition: transform ease-out 0.2s;
}

.card-shadow {
  box-shadow: 0px 3px 7px rgba(0, 0, 0, 0.25);
}
.card.inactive {
  visibility: hidden;
  /* box-shadow: none; */
}
