.phone-option {
  position: relative;
  z-index: 1000;
}
.phone-option__icon {
  width: 21px;
  height: 21px;
  background: #ff8515;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 3px;
}
.phone-option__modal {
  position: absolute;
  right: 45%;
  padding: 1rem 1.2rem 1rem 1rem;
  width: 73vw;
  bottom: 185%;
  background: white;
  border-radius: 2px;
  border: 1px solid #ff8515;
  box-shadow: 3px 3px 8px rgba(0, 0, 0, 0.25);
  transition: all 1s ease;
}

.phone-option__modal div {
  position: absolute;
  width: 20px;
  height: 29px;
  bottom: -15px;
  right: -1px;
  border-right: 1px solid #ff8515;
  z-index: 50;
  overflow: hidden;
}
.phone-option__modal div:after {
  content: "";
  position: absolute;
  top: 3px;
  right: -12px;
  width: 20px;
  height: 20px;
  transform: rotate(45deg);
  border: 1px solid #ff8515;
  border-left-color: transparent;
  background: white;
}
.phone-option__modal p {
  margin: 0;
  color: #595959;
  font-size: 0.9rem;
}
