.sommaire-quiz {
  padding: 0;
  border: none;
}
.sommaire-quiz__title {
  padding-bottom: 5px;
  border-bottom: 1px solid orange;
  position: relative;
  height: 10vh;
}
.sommaire-quiz__title-bg {
  background: deepskyblue;
  height: 97%;
  display: flex;
  align-items: center;
  background: linear-gradient(
      270deg,
      rgba(196, 196, 196, 0) -5.33%,
      #ffffff 51.87%
    ),
    url(/assets/img/arched-header_bg.jpg) center center;
}

.sommaire-quiz__title-bg .slide-prev {
  cursor: pointer;
}
.sommaire-quiz__title-bg .slide-prev * {
  stroke: var(--dark-green);
}

.sommaire-quiz__title-bg h2 {
  max-width: 50%;
  color: var(--dark-green);
  font-size: 1.5rem;
  line-height: 100%;
  margin: 0 0 0 0.5rem;
}
.sommaire-quiz h3 {
  position: absolute;
  top: 10vh;
}

.sommaire-quiz .title-bar {
  position: relative;
}

.sommaire-quiz .list-sommaire {
  padding-top: 0;
}
