.main-abonnement {
  position: absolute;
  top: 30vh;
  height: 60vh;
  margin: 0;
  padding-bottom: 0 !important;
  width: 100%;
  overflow: auto;
  padding-bottom: 10vh;
  z-index: 10;
}

.main-abonnement.tall {
  top: 21vh;
  height: 69vh;
}

@media (min-height: 750px) {
  .main-abonnement.tall {
    top: 18vh;
    height: 70vh;
  }
}

.main-abonnement .abonnement-step {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
}

.main-abonnement .abonnement-step .header h1 {
  margin: 0;
}
.main-abonnement .abonnement-step .content {
  flex-grow: 1;
  margin: 0;
}
.swiper-slide {
  overflow: hidden;
}

.main-abonnement.footerPicture {
  position: relative;
}
.main-abonnement.footerPicture::before {
  position: absolute;
  content: "";
  left: 0;
  right: 0;
  height: 20vh;
  bottom: 0;
  background: linear-gradient(
      180deg,
      #ffffff 7.56%,
      rgba(255, 255, 255, 0) 160.65%
    ),
    url("/assets/img/footer_bg.jpeg") no-repeat center center / cover;
}
