.abonnement-step.finalisation-abonnement {
  overflow: auto;
}
.abonnement-step.finalisation-abonnement .header h2 {
  margin-bottom: 0.5rem;
}

.finalisation-abonnement__code-abonnement {
  padding: 0 1rem 0.5rem;
}

.finalisation-abonnement__code-abonnement .code-input {
  position: relative;
  text-align: center;
  margin-bottom: 0.5rem;
}

.finalisation-abonnement__code-abonnement input {
  background: #ececec;
  border: 1px solid #c0c0c0;
  padding: 0.7rem;
  width: 55%;
  box-sizing: border-box;
  border-radius: 10px;
  outline: none;
}

.finalisation-abonnement__code-abonnement div .icon {
}

.finalisation-abonnement__code-abonnement .indication {
  margin: 0;
  font-weight: 500;
  font-size: 0.8rem;
  line-height: 1.2rem;
  font-style: italic;
  color: #a7a7a7;
}

.finalisation-abonnement__code-abonnement button {
  background: var(--dark-green);
  font-weight: 400;
  font-size: 1rem;
  border-radius: 5px;
  line-height: 1.8rem;
  margin: 0 auto;
  width: 60%;
  padding: 0.3rem;
}
.finalisation-abonnement__recapitulatif .title {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #13b582;
}

.finalisation-abonnement__recapitulatif .title p {
  margin: 0 1rem;
}

.finalisation-abonnement__recapitulatif .title hr {
  flex-grow: 1;
  border: 1px solid #13b582;
}

.finalisation-abonnement__recapitulatif .recap {
  font-size: 1rem;
  line-height: 1.7rem;
  color: rgba(77, 77, 77, 0.6);
  text-align: center;
}

.finalisation-abonnement__recapitulatif .recap p {
  margin: 0;
}

.finalisation-abonnement__recapitulatif .recap .classes {
  display: flex;
  padding: 0.5rem;
  margin: 0;
  list-style: none;
}
.finalisation-abonnement__recapitulatif .recap .classes.center {
  justify-content: center;
  padding: 0;
}

@media (min-height: 640px) {
  .abonnement-step.finalisation-abonnement {
    overflow: hidden;
  }
}

@media (min-height: 700px) {
  .finalisation-abonnement__code-abonnement {
    margin-bottom: 1rem;
    padding: 0 1rem 1rem;
  }

  .finalisation-abonnement__code-abonnement .code-input {
    margin-bottom: 1rem;
  }

  .finalisation-abonnement__code-abonnement input {
    padding: 1rem;
  }

  .finalisation-abonnement__code-abonnement .indication {
    font-size: 1rem;
    line-height: 1.5rem;
  }
}
@media (min-height: 800px) {
  .finalisation-abonnement__recapitulatif {
    margin-top: 2rem;
  }
  .finalisation-abonnement__recapitulatif .recap {
    padding: 2rem 0;
  }
}
@media (min-height: 900px) {
  .finalisation-abonnement {
    padding-top: 2rem;
  }
}
@media (min-height: 950px) {
  .finalisation-abonnement {
    padding-top: 3rem;
  }
}
@media (min-height: 990px) {
  .finalisation-abonnement {
    padding-top: 4rem;
  }
}
