.arched-header {
  position: fixed;
  z-index: 10;
  top: 0;
  height: 64vw;
  width: 160vw;
  top: -15.6vw;
  left: -30vw;
  margin-bottom: 1rem;
  padding: 0;
}

.arched-header::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border-radius: 50%;
  background: linear-gradient(
      180deg,
      #066877 20.63%,
      rgba(19, 181, 130, 0) 143.12%
    ),
    linear-gradient(0deg, #066877, #066877),
    url("/assets/img/arched-header_bg.jpeg") no-repeat center;
  background-blend-mode: normal, multiply, normal;
}

.back-icon {
  cursor: pointer;
  position: absolute;
  top: 18vw;
  left: 33vw;
}
