.abonnement {
  height: 100vh;
  width: 100vw;
  overflow: hidden;
}

.arched-header.small {
  height: 50vw;
  width: 160vw;
  top: -15.6vw;
  left: -30vw;
}

.abonnement footer {
  height: 10vh;
  position: fixed;
  background: red;
  z-index: 10;
  bottom: 0;
  width: 100vw;
}

.abonnement__swiper {
  height: 100%;
}

.abonnement .abonnement-step {
  display: flex;
  flex-direction: column;
}

.abonnement .abonnement-step .content {
  flex-grow: 1;
}

.abonnement .abonnement-step .header {
  text-align: center;
  margin-bottom: 0.5rem;
  padding: 0 1rem;
}

.abonnement .abonnement-step .header h2 {
  color: #066877;
  font-weight: 400;
  margin-bottom: 1rem;
}

.abonnement .abonnement-step .header hr {
  border: 1px solid #dcdcdc;
  width: 50vw;
  flex-grow: unset;
}

.abonnement .abonnement-step .content {
  display: flex;
  flex-direction: column;
  padding: 0 0 1rem 0;
  list-style: none;
}

.abonnement .abonnement-step .content .list-item {
  margin: 0.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  border-radius: 5px;
  border: 1px solid #f0f0f0;
  cursor: pointer;
}

.abonnement .abonnement-step .content .list-item::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  border-radius: 5px;
  border: 1px solid #f0f0f0;
}

.abonnement .content li p {
  color: white;
  font-size: 1.4rem;
  margin: 0;
  z-index: 1;
  line-height: 1.5rem;
}
