.eleves-option {
  display: flex;
  list-style: none;
  padding: 0.5rem 0;
  justify-content: space-evenly;
  margin: 0;
  overflow: hidden;
  position: relative;
}

.eleves-option__eleve {
  flex-grow: 1;
  flex-basis: 30%;
  padding: 0.5rem 0 0;
  cursor: pointer;
  height: auto;
}

.eleves-option__eleve:nth-child(2) {
  border-right: 0.5px solid #8b8b8b;
  border-left: 0.5px solid #8b8b8b;
}

.eleves-option__eleve--header {
  height: 40%;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 1rem;
}

.eleves-option__eleve--header h2 {
  font-size: 0.8rem;
  color: #2f2f2f;
  font-weight: normal;
  line-height: 170.69%;
  margin: 0;
}

.eleves-option__eleve--header hr {
  width: 70%;
  height: 1px;
  background: #c9c9c9;
  margin: 0;
  border-color: #c9c9c9;
  flex-grow: unset;
}

.eleve--header__avatar {
  margin: 0;
  padding: 0;

  margin-bottom: 0.5rem;
  position: relative;
}

.eleve--header__avatar-bg {
  width: 100%;
  height: 100%;
  width: 65px;
  height: 65px;
  border-radius: 50%;
}
.eleve--header__edit-icon {
  position: absolute;
  z-index: 1;
  bottom: -5px;
  right: 0;
}

.eleves-option__eleve--info p {
  margin: 0.5rem 0;
  font-size: 0.7rem;
  font-weight: normal;
  color: rgba(47, 47, 47, 1);
  padding: 0 0.5rem;
}

.eleves-option__eleve--info p.color-primaire,
.color-primaire {
  color: var(--color-primaire);
  font-weight: bold;
}
.eleves-option__eleve--info p.color-cem,
color-cem {
  color: var(--color-cem);
  font-weight: bold;
}
.eleves-option__eleve--info p.color-lycee,
.color-lycee {
  color: var(--color-lycee);
  font-weight: bold;
}
