@import url("https://fonts.googleapis.com/css2?family=Roboto:ital@1&display=swap");

:root {
  --primary-color: #0fd79c;
  --dark-primary-color: #137179;
  --blue-color: #007aff;
  --yellow-color: #f2c94c;
  --dark-green: #066877;
  --color-primaire: rgba(255, 133, 21, 0.8);
  --color-cem: rgba(80, 80, 80, 0.8);
  --color-lycee: rgba(5, 132, 250, 0.8);
}

* {
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
  -moz-user-select: none;
  webkit-user-select: none;
  ms-user-select: none;
  user-select: none;
}
#root {
  overflow: hidden;
}

html {
  height: 100%;
  font-family: "Roboto", sans-serif;
}

body {
  margin: 0;
  padding: 0;
}

.hide {
  display: none;
}

.no-hide {
  display: block;
}
.center {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.flex-col {
  flex-direction: column;
}

@media (min-width: 500px) {
  .app-user {
    display: none;
  }
}

h2,
h3 {
  font: inherit;
  margin: 0 0 0.5rem 0;
}
h2 {
  font-size: 1.3rem;
  font-weight: 700;
  line-height: 1.2;
}

h3 {
  font-size: 1.1rem;
  font-weight: 400;
}

.d-n {
  display: none;
}

