  justify-content: center;
}
.preview__actions {
  align-self: center;
  position: absolute;
  bottom: 15%;
  width: 100%;
  padding: 0 2rem;
  z-index: 100;
  margin: 0;
}

.swiper-pagination-custom-bullet {
  height: 12px;
  width: 12px;
  margin-right: 0.5rem !important;
  background: rgba(255, 255, 255, 0.33);
}
.swiper-pagination-bullet-active {
  background: white;
}

.preview__actions button,
.preview__actions a {
  width: 100%;
  margin-top: 1rem;
}

.preview-tab--img1 {
  background: radial-gradient(
      89.69% 41.81% at 46.27% 49.01%,
      rgba(255, 255, 255, 0) 0%,
      rgba(5, 52, 49, 0.79) 100%
