.backdrop {
  position: fixed;
  width: 100vw;
  height: 100vh;
  z-index: 50;
  background: rgba(0, 0, 0, 0.35);
  background-blend-mode: multiply;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.backdrop.high-zindex {
  z-index: 110;
}

.modal {
  position: fixed;
  z-index: 100;
  width: 80vw;
  height: 55vh;
  bottom: 50%;
  overflow: auto;
  right: 50%;
  background: #ffffff;
  box-shadow: 3px 4px 13px rgba(0, 0, 0, 0.25);
  border-radius: 8px;
  transform: translate(50%, 50%);
  padding: 0.3rem;
}

.modal.completelogin {
  width: 90vw;
  height: 50vh;
  overflow: hidden;
}

.modal::before {
  content: "";
  position: absolute;
  bottom: 0.3rem;
  left: 0.3rem;
  right: 0.3rem;
  height: 70%;
  background: linear-gradient(
      180deg,
      #ffffff 7.56%,
      rgba(255, 255, 255, 0) 160.65%
    ),
    url("/assets/img/footer_bg.jpeg") center center / cover;
  opacity: 0.63;
  border-radius: 5px;
}

.modal.completelogin::before {
  height: 45%;
}
.modal.high-zindex {
  z-index: 120;
}

@media (min-height: 630px) {
  .modal.completelogin {
    height: 45vh;
  }
}
@media (min-height: 700px) {
  .modal {
    height: 50vh;
  }
  .modal.completelogin {
    height: 40vh;
  }
}
@media (min-height: 750px) {
  .modal.completelogin {
    height: 35vh;
  }
}

@media (min-height: 785px) {
  .modal {
    height: 42vh;
  }
}

@media (min-height: 880px) {
  .modal.completelogin {
    height: 30vh;
  }
  .modal {
    height: 38vh;
  }
}

