.profile {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 40;
  height: 24vh;

  background: radial-gradient(
      22.36% 99.86% at 93.18% 1.95%,
      rgba(19, 181, 130, 0.82) 0%,
      rgba(6, 104, 119, 0.82) 100%
    ),
    url(/static/media/bg-prof.b31482c0.jpg) no-repeat center center;
  -webkit-backdrop-filter: blur(12px);
          backdrop-filter: blur(12px);
  background-size: cover;
}

.profile__cancel {
  right: 2rem;
  cursor: pointer;
  top: 35%;
  z-index: 50;
  width: 20px;
  height: 20px;
  background: transparent;
  display: flex;
  justify-content: space-around;
  align-items: center;
  border: none;
  outline: none;
  position: absolute;
}

.profile__cancel span {
  transition: all 0.4s ease;
}

.profile__cancel span:first-child {
  background: white;
  width: 100%;
  transform: rotate(45deg);
  position: absolute;
  height: 2px;
  z-index: 50;
}

.profile__cancel span:last-child {
  background: white;
  position: absolute;
  z-index: 50;
  width: 100%;
  transform: rotate(135deg);
  height: 2px;
}

.profile__cancel:hover span {
  background: var(--primary-color);
}
.profile__cancel:hover span:first-child {
  transform: rotate(225deg);
}
.profile__cancel:hover span:last-child {
  transform: rotate(315deg);
}

.profile .profile__content {
  width: 100vw;
