  overflow: hidden;
  position: relative;
}

.profile .profile__content .profile__menu {
  cursor: pointer;
  width: 24px;
  height: 24px;
  background: transparent;
  position: absolute;
  top: calc(24vh * 0.45 - 0.7rem);
  outline: none;
  border: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0;
}

.profile__menu:focus,
.profile__menu:active {
  background: none;
}

.profile .profile__content .profile__menu span {
  background: white;
  display: block;
  height: 2px;
  margin-bottom: 4px;
  width: 100%;
}

.profile .profile__content .profile__info {
  position: absolute;
  bottom: 1rem;
  color: white;
  padding-left: 2px;
  font: inherit;
}

.profile .profile__info h2 {
  margin-bottom: 4px;
