}
.slide-in-left-exit {
  transform: translateX(0);
}
.slide-in-left-exit-active {
  transform: translateX(-100%);
  transition: 0.4s;
}

.slide-videos-enter {
  transform: translateX(100%);
}
.slide-videos-enter-active {
  transform: translateX(0%);
  transition: 0.4s;
}
.slide-videos-exit {
  transform: translateX(0%);
  transition: 0.4s;
}
.slide-videos-exit-active {
  transform: translateX(100%);
