.item__thumbnail {
  text-decoration: none;
  position: relative;
  display: block;
  outline: none;
  border: none;
  cursor: pointer;
  text-decoration: none;
  color: white;
  border: 1px solid #ff8515;
  border-radius: 0px 15px 1px;
  height: 100%;
  margin-right: 1rem;
  text-align: center;
  /* width: 50vw; */
}
.item__thumbnail-title {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  top: 30%;
  line-height: 1.2rem;
  padding: 1rem;
  margin: 0;
}

.item__thumbnail-title p {
  margin: 0;
  text-shadow: 0px 2px 8px rgba(0, 0, 0, 0.4);
  font-size: 1.4rem;
  font-weight: bold;
}

.item__thumbnail-matiere {
  position: absolute;
  min-width:25%;
  height: 10%;
  left: 0;
  top: -10%;
  display: flex;
  justify-content: center;
  /* padding: 0.1rem 0.6rem 0.2rem 0.4rem; */
  color: white;
  background: #ff8515;
  border-top-right-radius: 10px;
}

.item__thumbnail-matiere p {
  margin: 0;
  align-self: center;
  font-size: 13px;
  line-height: 12px;
  text-align: center;
}

.item__thumbnail-questions {
  position: absolute;
  right: 0;
  bottom: 0;
  padding: 0.2rem 0.2rem 0.1rem 0.4rem;
  color: white;
  width: 90px;
  border-top-left-radius: 10px;
  background: #ff8515;
}

.item__thumbnail-questions p {
  margin: 0;
  padding: 0;
  text-align: center;
  line-height: 14px;
  font-size: 12px;
}

@media (min-width: 360px) {
  .item__thumbnail {
    width: 45vw;
  }
}
@media (min-width: 380px) and (max-height: 690px) {
  .item__thumbnail {
    width: 40vw;
  }
}

@media (min-height: 720px) {
  .item__thumbnail {
    width: 50vw;
  }
}
@media (min-height: 780px) {
  .item__thumbnail {
    width: 55vw;
  }
}
@media (min-height: 860px) {
  .item__thumbnail {
    width: 60vw;
  }
}
